import React, { useEffect, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { PageShell } from './PageShell';
import { supabase } from '@/lib/supabase';

interface Product {
  id: string;
  name: string;
  price: number;
  stock: number;
}

interface CartItem {
  product: Product;
  quantity: number;
}

interface NewSaleScreenProps {
  cart: CartItem[];
  onAdd: (product: Product) => void;
  onRemove: (product: Product) => void;
  onFinalize: () => void;
  onBack: () => void;
}

export const NewSaleScreen: React.FC<NewSaleScreenProps> = ({
  cart,
  onAdd,
  onRemove,
  onFinalize,
  onBack,
}) => {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchProducts();
  }, []);

  const fetchProducts = async () => {
    setLoading(true);
    const { data, error } = await supabase
      .from('products')
      .select('id, name, price, stock')
      .eq('is_active', true)
      .gt('stock', 0) // Only show products with stock
      .order('name');

    if (data) {
      setProducts(data);
    }
    setLoading(false);
  };

  const total = cart.reduce(
    (acc, item) => acc + item.product.price * item.quantity,
    0
  );

  const formatCurrency = (value: number) =>
    value.toLocaleString('pt-BR', {
      style: 'currency',
      currency: 'BRL',
      minimumFractionDigits: 2,
    });

  return (
    <PageShell title="Nova venda" onBack={onBack}>
      <div className="flex flex-col gap-3 pb-24">
        <div className="rounded-2xl border border-slate-800/80 bg-slate-950/60 divide-y divide-slate-800/80">
          {loading ? (
            <div className="p-4 text-center text-xs text-slate-500">
              Carregando produtos...
            </div>
          ) : products.length === 0 ? (
            <div className="p-4 text-center text-xs text-slate-500">
              Nenhum produto dispon√≠vel.
            </div>
          ) : (
            products.map((p) => {
              const cartItem = cart.find((c) => c.product.id === p.id);
              return (
                <div
                  key={p.id}
                  className="flex items-center justify-between px-3 py-2.5 text-xs"
                >
                  <div className="flex flex-col">
                    <span className="text-slate-100">{p.name}</span>
                    <span className="text-[11px] text-slate-500">
                      {formatCurrency(p.price)}
                    </span>
                  </div>
                  <div className="flex items-center gap-2">
                    {cartItem && (
                      <button
                        onClick={() => onRemove(p)}
                        className="h-7 w-7 rounded-full border border-slate-700/80 text-xs flex items-center justify-center"
                      >
                        ‚àí
                      </button>
                    )}
                    <motion.span
                      key={cartItem?.quantity ?? 0}
                      initial={{ scale: 0.9, opacity: 0 }}
                      animate={{ scale: 1, opacity: 1 }}
                      className="min-w-[20px] text-center text-[11px] text-slate-300"
                    >
                      {cartItem?.quantity ?? 0}
                    </motion.span>
                    <button
                      onClick={() => onAdd(p)}
                      className="h-7 w-7 rounded-full bg-slate-100 text-slate-950 text-xs flex items-center justify-center"
                    >
                      Ôºã
                    </button>
                  </div>
                </div>
              );
            })
          )}
        </div>
      </div>

      <AnimatePresence>
        {total > 0 && (
          <motion.div
            initial={{ y: 60, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            exit={{ y: 60, opacity: 0 }}
            transition={{ type: 'spring', stiffness: 380, damping: 32 }}
            className="fixed inset-x-0 bottom-0 px-4 pb-4 max-w-md mx-auto"
          >
            <div className="rounded-2xl border border-emerald-500/30 bg-slate-950/95 backdrop-blur shadow-lg shadow-emerald-500/20 px-3.5 py-3 flex items-center justify-between text-xs">
              <div className="flex flex-col">
                <span className="text-[11px] text-slate-400">
                  Total da venda
                </span>
                <span className="text-base font-semibold text-emerald-400">
                  {formatCurrency(total)}
                </span>
              </div>
              <button
                onClick={onFinalize}
                className="rounded-2xl bg-emerald-500 text-emerald-950 text-xs font-medium px-4 py-2 flex items-center gap-1"
              >
                Finalizar
                <span>‚Üí</span>
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </PageShell>
  );
};
