import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { PageShell } from "./PageShell";
import { supabase } from "@/lib/supabase";

type NavTab = "dashboard" | "products" | "profile";

interface DashboardScreenProps {
  onNewSale: () => void;
  onGoProducts: () => void;
  onGoSalesHistory: () => void;
  activeTab?: NavTab;
  onNavigate?: (tab: NavTab) => void;
}

export const DashboardScreen: React.FC<DashboardScreenProps> = ({
  onNewSale,
  onGoProducts,
  onGoSalesHistory,
  activeTab,
  onNavigate,
}) => {
  const [metrics, setMetrics] = useState({
    todaySales: 0,
    todayRevenue: 0,
    pixRevenue: 0,
    pixCount: 0
  });

  useEffect(() => {
    const fetchMetrics = async () => {
      const { data, error } = await supabase
        .from('store_analytics')
        .select('*')
        .maybeSingle();

      if (data) {
        setMetrics({
          todaySales: data.today_sales || 0,
          todayRevenue: data.today_revenue || 0,
          pixRevenue: 0,
          pixCount: 0
        });
      }
    };

    fetchMetrics();
  }, []);

  const formatCurrency = (value: number) =>
    value.toLocaleString("pt-BR", {
      style: "currency",
      currency: "BRL",
      minimumFractionDigits: 2,
    });

  return (
    <PageShell 
        title="Resumo do dia" 
        activeTab={activeTab} 
        onNavigate={onNavigate}
    >
      <section className="flex flex-col gap-4">
        <motion.button
          whileTap={{ scale: 0.98 }}
          onClick={onNewSale}
          className="w-full rounded-2xl bg-blue-600 text-white text-sm font-medium py-4 flex items-center justify-center gap-2 shadow-sm hover:bg-blue-700 transition-colors"
        >
          <span className="text-lg">Ôºã</span>
          Nova venda
        </motion.button>

        <div className="grid grid-cols-2 gap-4">
          <motion.div
            layout
            className="rounded-2xl border border-gray-200 bg-white p-5 flex flex-col gap-2 shadow-sm"
          >
            <span className="text-xs text-gray-500 font-medium">Vendas de hoje</span>
            <span className="text-2xl font-semibold text-gray-900">{formatCurrency(metrics.todayRevenue)}</span>
            <span className="text-xs text-blue-600">{metrics.todaySales} vendas</span>
          </motion.div>
          <motion.div
            layout
            className="rounded-2xl border border-gray-200 bg-white p-5 flex flex-col gap-2 shadow-sm"
          >
            <span className="text-xs text-gray-500 font-medium">Recebido via PIX</span>
            <span className="text-2xl font-semibold text-gray-900">{formatCurrency(metrics.pixRevenue)}</span>
            <span className="text-xs text-gray-500">{metrics.pixCount} pendentes</span>
          </motion.div>
        </div>
      </section>

      <section className="flex flex-col gap-4">
        <div className="flex items-center justify-between">
          <span className="text-sm font-medium text-gray-700">Atalhos r√°pidos</span>
        </div>
        <div className="grid grid-cols-3 gap-3">
          <button
            onClick={onNewSale}
            className="rounded-2xl border border-gray-200 bg-white p-4 flex flex-col items-start gap-2 hover:border-gray-300 hover:shadow-sm transition-all"
          >
            <span className="rounded-full bg-blue-100 text-blue-600 text-base px-2 py-1">
              ‚ö°
            </span>
            <span className="text-xs text-gray-700 font-medium">Nova venda</span>
          </button>
          <button
            onClick={onGoProducts}
            className="rounded-2xl border border-gray-200 bg-white p-4 flex flex-col items-start gap-2 hover:border-gray-300 hover:shadow-sm transition-all"
          >
            <span className="rounded-full bg-gray-100 text-gray-600 text-base px-2 py-1">
              üì¶
            </span>
            <span className="text-xs text-gray-700 font-medium">Produtos</span>
          </button>
          <button
            onClick={onGoSalesHistory}
            className="rounded-2xl border border-gray-200 bg-white p-4 flex flex-col items-start gap-2 hover:border-gray-300 hover:shadow-sm transition-all"
          >
            <span className="rounded-full bg-gray-100 text-gray-600 text-base px-2 py-1">
              üßæ
            </span>
            <span className="text-xs text-gray-700 font-medium">Vendas</span>
          </button>
        </div>
      </section>
    </PageShell>
  );
};
